@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;700;800;900&family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);

:root {
  --background-color:#040404;
  --foreground-color: #F2F2F2;  
  --foreground-grey: #909090;
  font-family: Inter;
}

* {
  margin:0px;
  padding:0px;
}

body {
  background-color: #040404;
  background-color: var(--background-color);
  color: #F2F2F2;
  color: var(--foreground-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* scroll-behaviour: smooth; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.nav {
    font-size: 12px;
    font-weight: normal;
    letter-spacing: 2px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content:space-between;
            justify-content:space-between;
    padding: 40px 140px;
}

.nav b {
    font-weight: 600;
}

/* .nav b:hover {
    font-weight: 100;
} */

.nav ul {
    list-style: none;
    display: inline;
}

.nav li {
    display: inline-block;
    padding-left: 40px;
}

.nav a {
    color:var(--foreground-color);
    text-decoration: none;
    transition: all 0.3s ease-in;
}

.nav a:hover {
    /* color:var(--foreground-grey); */
    -webkit-animation: chroma  ease-in 0.2s normal forwards;
            animation: chroma  ease-in 0.2s normal forwards;
}

@-webkit-keyframes chroma{
    100%{text-shadow:-1px 0px 0px #1AD7E0, -3px 0px 0px #0D10DC,1px 0px 0px #d68010 ,3px 0px 0px #CB0C0C;}
    
}

@keyframes chroma{
    100%{text-shadow:-1px 0px 0px #1AD7E0, -3px 0px 0px #0D10DC,1px 0px 0px #d68010 ,3px 0px 0px #CB0C0C;}
    
}
.home {
    padding-top: 50px;
    text-align: center;
    font-size: 14px;
    font-weight: 200;
    letter-spacing: 0.6px;
}

.home h3{
    font-size: 80px;
    text-shadow:   
    -1.5px 0px 0px #1AD7E0,
    -3px 0px 0px #0D10DC,
     1.5px 0px 0px #d68010 ,
     3px 0px 0px #CB0C0C;
}
.row .row-reverse {
    display: -webkit-flex;
    display: flex;
}

.row {
    -webkit-flex-direction: row;
            flex-direction: row;
    color:red;
}

.row-reverse {
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    color:pink;
}


.archieve-heading{
    max-width: 510px;
    font-size: 25px;
    letter-spacing: 2px;
    padding: 10px 0px;
    transition: 0.5s all ease-in-out;
}


.archieve-heading b {
    font-weight: 800;
}

.archieve-date{
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 200;
    padding: 12px 0px;
}


.left img {
    max-width:510px;
}
.dev-try{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}


/* chromatic aberation */
.dev-try h1{
    font-size: 100px;
    text-transform: uppercase;
    transition:0.2s ;
}

.dev-try h1:hover{

    -webkit-animation: chromatic  ease-in 0.2s normal forwards;

            animation: chromatic  ease-in 0.2s normal forwards;
    cursor: pointer;
}



@-webkit-keyframes chromatic{
    100%{text-shadow:   -1px 0px 0px #1AD7E0, -2px 0px 0px #0D10DC,1px 0px 0px #d68010 ,2px 0px 0px #CB0C0C;}
    
}



@keyframes chromatic{
    100%{text-shadow:   -1px 0px 0px #1AD7E0, -2px 0px 0px #0D10DC,1px 0px 0px #d68010 ,2px 0px 0px #CB0C0C;}
    
}
